
import Global from '@/mixins/GlobalMixin';
import { Component, PropSync } from 'vue-property-decorator';
import { TCategoryOfBody } from '@/controllers/CategoryOfBodyController';

@Component
export default class ModalDeleteBody extends Global {
  @PropSync('show') readonly dialog!: boolean;
  @PropSync('selected', { required: true }) selectedBody!: TCategoryOfBody;
  loading = false;

  apply() {
    this.$emit('select');
  }

  cancel() {
    this.$emit('cancel');
  }
}
