
import { Component, Prop, Ref } from 'vue-property-decorator';
import { mixins } from 'vue-class-component';
import Helper from '@/mixins/Helper';
import { Mutation, State } from 'vuex-class';
import { TUnitsOfMeasurementFilterViewModel } from '@/controllers/UnitsOfMeasurementController';

type TFilterType = 'queryString'

export type TIncomingRouteName = 'New' | 'Handled' | 'Saved';

@Component
export default class Filters extends mixins(Helper) {
  @State readonly filters!: any;
  @Mutation('setFilters') setFilters!: (options: any) => void;
  @State readonly loading!: any;
  @Mutation('setLoading') setLoading!: (options: any) => void;

  get filterModel(): TUnitsOfMeasurementFilterViewModel {
    return this.filters.unitsofmeasurement;
  }

  set filterModel(value: TUnitsOfMeasurementFilterViewModel) {
    this.setFilters({ name: 'unitsofmeasurement', value });
    this.$emit('change');
  }

  get loadingModel() {
    return this.loading.unitsofmeasurement;
  }

  get isListLoading() {
    return this.loadingModel.list;
  }

  set isListLoading(value: boolean) {
    this.setLoading({ category: 'unitsofmeasurement', name: 'list', value });
  }

  created(): void {
    this.queryString = this.filterModel.queryString || '';
  }

  queryString = '';

  clearFilters(): void {
    this.queryString = '';

    this.filterModel = {
      queryString: ''
    };

    this.$emit('change');
  }

  apply(filter: TFilterType): void {
    this.isListLoading = true;
    switch (filter) {
      case 'queryString':
        this.filterModel = { ...this.filterModel, queryString: this.queryString };
        break;
    }

    this.$emit('change');
  }

  cancel(filter: TFilterType): void {
    this.isListLoading = true;
    switch (filter) {
      case 'queryString':
        this.queryString = this.filterModel.queryString || '';
        break;

      default:
        break;
    }
  }

  clear(filter: TFilterType): void {
    this.isListLoading = true;
    switch (filter) {
      case 'queryString':
        this.queryString = '';
        this.filterModel = { ...this.filterModel, queryString: '' };
        break;

      default:
        break;
    }

    this.$emit('change');
  }

  onKeydown(e: KeyboardEvent): void {
    if (e.key === 'Enter') {
      this.apply('queryString');
    }
  }
}
